<template>
  <div>
    <nav class="navbar navbar-light bg-light fixed-top">
      <label>Jason & Marc's ToDo List</label>
      <button class="btn btn-outline pull-right" type="button" float="right" v-on:click="signout()">Sign Out</button>
    </nav>
    <div class="side-list">
      <label>Users:</label>
      <ul class="list-group">
        <button type="button" class ="list-group-item" v-for="user in this.users">{{user.displayName}}</button>
      </ul>
    </div>
    <div class="container">
      <ul class="list-group">
        <li class ="list-group-item" v-for="item in this.listItems">{{item.item}}</li>
      </ul>
    </div>
  </div>
</template>

<script>
  export default {
    data(){
      return {
        users: '',
        listItems: ''
      }
    },
    created: function() {
      if(this.$cookies.get('user')== null){
        alert("Please sign in")
        this.$router.push("/signin")
      }
      this.getUsers()
      this.getList()
    },
    methods: {
      getUsers(){
        axios.get('https://info3103.cs.unb.ca:36479/users', { headers: {
          "Access-Control-Allow-Origin": '*'
        }})
        .then(response => {
          if(response.status = 200){
            console.log(response.data.users)
            this.users = response.data.users
            console.log(this.users)
          }
        }).catch(error => {
          console.log(error)
        })
      },
      signout(){
        axios.delete('https://info3103.cs.unb.ca:36479/signin', { headers: {
          "Access-Control-Allow-Origin": '*'
        }})
        .then(response => {
          if(response.status = 204){
            this.$cookies.remove('user')
            this.$cookies.remove('userId')
            this.$router.push("/signin")
          }
        })
      },
      getList(){
        console.log(this.$cookies.get('userId') + 'I told ya so')
        axios.get('https://info3103.cs.unb.ca:36479/users/' + this.$cookies.get('userId') + '/list', { headers: {
          "Access-Control-Allow-Origin": '*'
        }})
        .then(response => {
          this.listItems = response.data.listItems
        })
      }
    }
  }
</script>

